.TodoSearch {
    border-radius: 5px;    
    border: solid 2px  #837895;
    text-align: center;
    background: #F9FBFC;    
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;    
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-color: #ffc300;
  }